.checkbox {
  margin: 0;
}
.checkbox label {
  padding-left: 6px;
  font-size: 1.1em;
}
.checkbox label:before {
  top: 0px;
  outline: 0;
  content: '';
  width: 17px;
  height: 17px;
  border-radius: 3px !important;
  border: 1px solid #7d8a99 !important;
}
.checkbox input:checked + label:after {
  background: url("../img/checkbox-active.png") no-repeat center #fff !important;
  content: "" !important;
  outline: 0;
  top: 0px;
  padding: 0;
  width: 17px;
  height: 17px;
  border-radius: 3px !important;
  border: 1px solid #ff6e00 !important;
}
